@import 'sass/bootstrap';
@import 'sass/font-awesome';
@import 'sass/hover-dropdown-menu';
@import 'sass/owlcarousel';
@import 'sass/owl.theme.default';
@import 'sass/switcher';
@import 'sass/jquery.bxslider';
@import 'sass/style';
@import 'sass/lightbox';
@import 'sass/responsive';
@import 'sass/color';

.hide-me {
  display: none;
}

.footer-bottom-bar {
  background-color: #202020;
}

.footer-main-section a:hover {
  color: #1bbc9b;
}

#productsgriads .product-item:nth-child(4n+1) {
  clear: left;
}

.Faqs-box .blog-paragraph-text p {
  font-size: 16px!important;
}

.blog-paragraph-text h4 {
  margin-top: 20px;
  margin-bottom: 6px;
}

.blog-paragraph-text ul {
  list-style: initial;
}

.bg-yellow > div {
	background-color: lighten(#fffdd0,4%) !important;
}

.banner-section .special-style-semidark:before {
	background-color: #1bbc9b;
}

.dark-only.dark-only.dark-only:hover {
	background-color: #202020;
}

.navbar-default .navbar-nav > .open > a, .navbar-default .navbar-nav > .open > a:focus, .navbar-default .navbar-nav > .open > a:hover, .nav .open > a, .nav .open > a:focus, .nav .open > a:hover, .dropdown-menu > li > a:focus, .dropdown-menu > li > a:hover {
	color: rgb(27, 188, 155) !important;
}
