@import url('https://fonts.googleapis.com/css?family=Raleway:300,400,500,700,800,900|Roboto:300,400,500,700,900');







/*
=====================
1. Body
=====================
*/

body {
  font-family: 'Raleway', sans-serif;
  font-size: 16px;
  color: #777;
  line-height: 1.6em;
  background: #ffffff;
  -webkit-font-smoothing: antialiased;
  max-width: 100%;
  overflow-x: hidden;
}








/*
=====================
2. Default
=====================
*/

img {
  max-width: 100%;
}

a {
  text-decoration: none;
  cursor: pointer;
  color: inherit;
  -webkit-transition: all 0.45s ease-in-out;
  transition: all 0.45s ease-in-out;
}

ul {
  list-style: none;
}

a:hover,
a:focus,
a:visited {
  text-decoration: none;
  outline: none;
  -webkit-transition: all 0.45s ease-in-out;
  transition: all 0.45s ease-in-out;
}

p,
em {
  font-family: 'Raleway', sans-serif;
  font-size: 16px;
  line-height: 1.6em;
  color: #777777;
  text-align: initial;
  font-weight: 500;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  position: relative;
  font-family: 'Roboto', sans-serif;
  font-weight: 500;
  margin: 0px;
  background: none;
  line-height: 1.6em;
}

input[type=text],
input[type=email],
input[type=search],
input[type=password],
input[type=number],
select {
  font-family: 'Raleway', sans-serif;
  width: 100%;
  padding: 14px 10px;
  float: left;
  position: relative;
  outline: none;
  height: 33px;
  font-size: 14px;
  color: #777777;
  background-color: #fff;
  border: none;
  font-weight: 500;
  border-bottom: 1px solid #fff;
}

.search-box-top-bar input.search_terms {
  font-family: 'Raleway', sans-serif;
  width: 100%;
  float: right;
  position: relative;
  outline: none;
  height: 34px;
  font-size: 12px;
  padding: 12px 14px;
  background-color: #404040;
  border: none;
  color: #fff;
  max-width: 200px;
}

.search-box-top-bar input.search_terms::-webkit-input-placeholder {
  color: #969595;
}

.search-box-1 input {
  font-family: 'Raleway', sans-serif;
  width: 100%;
  padding: 12px 20px;
  float: left;
  position: relative;
  outline: none;
  height: 33px;
  font-size: 12px;
  color: #a3a2a2;
  background-color: #eee;
  border: none;
  border-bottom: 1px solid #fff;
}

.select-box select {
  font-family: 'Raleway', sans-serif;
  width: 100%;
  padding: 0;
  float: left;
  position: relative;
  outline: none;
  height: 33px;
  font-size: 14px;
  color: #a3a2a2;
  background-color: #fff;
  font-weight: 500;
  border: none;
}

.sow-page-secton select {
  float: none;
}

textarea {
  font-family: 'Raleway', sans-serif;
  width: 100%;
  padding: 12px 20px;
  float: left;
  position: relative;
  outline: none;
  font-size: 14px;
  color: #a3a2a2;
  background-color: #fff;
  border: 1px solid #f1f1f1;
}

.checbox-section input[type="checkbox"]+label:before {
  border: 1px solid #f1f1f1;
  content: "\00a0";
  display: inline-block;
  font: 16px/1em sans-serif;
  height: 16px;
  margin: 0 15px 0 0;
  padding: 0;
  vertical-align: top;
  width: 16px;
  margin-top: 5px;
}

.checbox-section input[type="checkbox"]:checked+label:before {
  color: #fff;
  content: "\2713";
  text-align: center;
  font-size: 11px;
  padding-top: 2px;
}

.checbox-section input[type="checkbox"]:checked+label:after {
  font-weight: bold;
}

.checbox-section input[type="checkbox"] {
  display: none;
}

.order-table-cell.box input {
  border: 1px solid #f1f1f1;
}

.Payment-Method-section .checbox-section input[type="checkbox"]+label:before {
  border: 1px solid #f1f1f1;
  content: "\00a0";
  display: inline-block;
  font: 16px/1em sans-serif;
  height: 20px;
  margin: 0 15px 0 0;
  padding: 0;
  vertical-align: top;
  width: 20px;
  margin-top: 5px;
  background: #fff;
}

.Payment-Method-section .checbox-section input[type="checkbox"]:checked+label:before {
  color: #fff;
  content: "\2713";
  text-align: center;
  font-size: 11px;
  padding-top: 4px;
}

.product-input-type input {
  width: 90%;
}

.quick-vive-popap .product-input-type input {
  width: 97%;
}

input[type="text"]::-webkit-input-placeholder,
input[type=email]::-webkit-input-placeholder,
input[type=search]::-webkit-input-placeholder,
input[type=password]::-webkit-input-placeholder,
input[type=number]::-webkit-input-placeholder,
textarea::-webkit-input-placeholder {
  color: #777777;
}

input[type=text]::-moz-placeholder,
input[type=email]::-moz-placeholder,
input[type=search]::-moz-placeholder,
input[type=password]::-moz-placeholder,
input[type=number]::-moz-placeholder,
textarea::-moz-placeholder {
  color: #777777;
}

input[type=text]:-ms-input-placeholder,
input[type=email]:-ms-input-placeholder,
input[type=search]:-ms-input-placeholder,
input[type=password]:-ms-input-placeholder,
input[type=number]:-ms-input-placeholder,
textarea:-ms-input-placeholder {
  color: #777777;
}

input[type=text]:-moz-placeholder,
input[type=email]:-moz-placeholder,
input[type=search]:-moz-placeholder,
input[type=password]:-moz-placeholder,
input[type=number]:-moz-placeholder,
textarea:-moz-placeholder {
  color: #777777;
}

.itg-button {
  padding: 6px 30px;
  background: #202020;
  display: inline-block;
  font-size: 16px;
  font-family: 'Roboto', sans-serif;
  font-weight: 400;
  color: #fff;
  text-transform: capitalize;
  -webkit-transition: all 0.45s ease-in-out;
  -moz-transition: all 0.45s ease-in-out;
  -o-transition: all 0.45s ease-in-out;
  -ms-transition: all 0.45s ease-in-out;
  border: none;
}

.quick-vive-popap .itg-button {
  padding: 6px 30px;
  border-radius: 0px;
  font-size: 14px;
  line-height: 21px;
  margin-left: 10px;
}

.product-box-btm-blog a.itg-button.pro.left {
  padding: 6px 30px;
  border-radius: 0px;
  font-size: 14px;
  line-height: 21px;
  margin-left: -8px;
}

.itg-button:hover {
  color: #fff;
}

.positionR {
  position: relative;
}

.positionA {
  position: absolute;
}

.positionF {
  position: fixed;
}

.positionS {
  position: static;
}

.clear {
  clear: both;
}

.grey-bgs {
  background-color: #eee;
}








