.top-bars-icon-section {
  width: 100%;
  float: left;
  position: relative;
}


.top-icons-box1 {
  width: 30%;
  float: left;
  position: relative;
}

.top-icons-box2 {
  width: 70%;
  float: left;
  position: relative;
}

.top-icons-box1 a {
  background: #eeeeee;
  width: 33px;
  height: 33px;
  border-radius: 100%;
  float: left;
  margin-top: 6px;
}

.top-icons-box1 a i {
  text-align: center;
  display: block;
  padding: 12px 0px;
  color: #969595;
  font-size: 12px;
  padding-left: 1px;
}

.top-icon-box3 {
  width: 100%;
  float: left;
  position: relative;
  margin: 7px 0px;
}

.top-icon-box3 ul {
  padding-left: 0;
  margin-bottom: 0;
  float: right;
}

.top-icon-box3 li a {
  width: 35px;
  height: 35px;
  display: inline-block;
  background-color: #eeeeee;
  border-radius: 100%;
  -webkit-transition: all 0.45s ease-in-out;
  -moz-transition: all 0.45s ease-in-out;
  -o-transition: all 0.45s ease-in-out;
  -ms-transition: all 0.45s ease-in-out;
}

.top-icon-box3 li a i:hover {
  color: #fff;
}

.top-icon-box3 li i {
  text-align: center;
  display: block;
  padding: 12px 0px;
